.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;

  background: rgba(182, 203, 215, 0.3);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.18);

  padding: 10px 40px 10px 40px;
}

.header .header_logo {
  max-width: 50px;
}
.container {
  display: flex;
  align-items: center;
}

.avatarHeader {
  margin: 0 10px 0 0;
  width: 30px;
  height: 30px;
  border-radius: 50%;
}
